import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { PermissionSet, Role } from 'shared-code-lib';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-role-permissions',
  templateUrl: './role-permissions.component.html',
  styleUrls: ['./role-permissions.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class RolePermissionsComponent implements OnInit {
  @Input() role: Role;

  available = [];
  current = [];

  constructor(private hydraApi: HydraApiService, private userState: UserStateService) {}

  ngOnInit() {
    this.hydraApi.getPermissionSets(this.permissionSetsLoaded, this.permissionSetsLoadFailed);
  }

  permissionSetsLoaded = (permissionSets: PermissionSet[]): void => {
    this.available = permissionSets;

    for (const ps of permissionSets) {
      if (this.role.permissions.find((rp) => rp == ps.primaryPermission)) {
        this.current.push(ps);
      }
    }
  };

  permissionSetsLoadFailed = (): void => {};

  currentChanged = (items: Array<PermissionSet>): void => {
    this.role.permissions = [];

    for (const item of items) {
      for (const permission of item.permissions) {
        this.role.addPermission(permission);
      }
    }
  };
}
