import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, MetaDataType, Role, Tab } from 'shared-code-lib';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control, User } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-role',
  templateUrl: './admin-role.component.html',
  styleUrls: ['./admin-role.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminRoleComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;
  activeTab = 'permissions';
  tabs = new Array<Tab>();

  fieldName: FieldDefinition;

  saveButton: Control;
  cancelButton: Control;

  role: Role;

  users = [];
  members = [];

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService
  ) {}

  ngOnInit() {
    this.createButtons();
    this.createFields();
    this.createTabs();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.loadRole();
      })
    );
  }

  createTabs = (): void => {
    this.tabs.push(new Tab('permissions', this.translateService.instant('admin-role.permissions')));
    this.tabs.push(new Tab('users', this.translateService.instant('admin-role.users')));
  };

  loadUsers = (): void => {
    this.hydraApi.getUsers(this.usersLoaded, this.userLoadFailed);
  };

  usersLoaded = (users: Array<User>): void => {
    this.users = users;
    this.members = [];

    for (const u of users) {
      if (u.roleIds.find((roleId) => roleId == this.role.id)) {
        this.members.push(u);
      }
    }
  };

  userLoadFailed = (): void => {};

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.translateService.get('Metadata.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Metadata.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  onSave = () => {
    if (this.role.id) {
      this.hydraApi.updateRole(this.role, this.roleSaved, this.roleSaveFailed);
    } else {
      this.hydraApi.createRole(this.role, this.roleSaved, this.roleSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  roleSaved = (result: ApiResult) => {
    if (result.success) {
      this.saveMembers(<Role>result.object);
    } else {
      this.spinner.hide();
      this.errorsService.showErrorsModal(result.errors, 'admin-role.validation-errors');
    }
  };

  roleSaveFailed = (err: any) => {
    this.toastr.error(this.translateService.instant('admin-role.save-failed'));
    this.spinner.hide();
  };

  saveMembers = (role: Role): void => {
    this.hydraApi.updateRoleMembers(role, this.members, this.roleMembersSaved, this.roleMembersSaveFailed);
  };

  roleMembersSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translateService.instant('admin-role.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-role.failed-save-members');
    }

    this.spinner.hide();
  };

  roleMembersSaveFailed = (err: any) => {
    this.toastr.error(this.translateService.instant('admin-role.save-failed'));
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.Roles'
    );
  };

  private loadRole = () => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.hydraApi.getRole(this.routeParams.roleid, this.roleLoaded, this.loadFailed);

      return;
    }

    const role = new Role({
      id: '',
      tenantId: '',
      name: '',
      permissions: new Array<string>(),
      dateCreatedUTC: new Date(),
      dateModifiedUTC: new Date(),
    });

    this.roleLoaded(role);
    this.loadUsers();
  };

  private roleLoaded = (r: Role) => {
    if (this.routeData.type === 'duplicate') {
      r.name = `${this.translateService.instant('Admin.CopyOf')} ${r.name}`;
      r.id = '';
    }

    this.role = r;

    this.loadUsers();
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-role.name'),
      placeholder: this.translateService.instant('admin-role.name'),
      required: true,
    });
  };

  private loadFailed = () => {};

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
