import { Component, OnInit, Input, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { Role } from 'shared-code-lib';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { User } from '../../../blocks/services/models';

@Component({
  selector: 'app-role-users',
  templateUrl: './role-users.component.html',
  styleUrls: ['./role-users.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class RoleUsersComponent implements OnInit {
  @Input() role: Role;
  @Input() users: Array<User>;
  @Input() members: Array<User>;
  @Output() membersChange = new EventEmitter<Array<User>>();

  constructor(private hydraApi: HydraApiService) {}

  ngOnInit() {}

  currentChanged = (users: Array<User>): void => {
    this.members = [];

    for (const user of users) {
      this.members.push(user);
    }

    this.membersChange.next(this.members);
  };
}
